  height: 100%;
  overflow-y: auto;
}
.am-tabs-default-bar-tab {
  height: 50px;
}
.app-tab-item {
  display: block;
  text-align: center;
}
.app-tab-item .icon {
  line-height: 1;
  text-align: center;
}
.app-tab-item .icon .we-icon-lg {
  width: 24px;
  height: 24px;
}
.app-tab-item .text {
  text-align: center;
  line-height: 1;
  font-size: 12px;
  line-height: 12px;
}
.app-tab-item .weicon + .am-badge-text {
  top: 0 !important;
}
.app-tab-item .weicon + .am-badge-dot {
  top: 0 !important;
}
.am-navbar-right .we-icon-lg {
  cursor: pointer;
  line-height: 36px;
  display: inline-block;
}
.am-navbar {
  background-color: #393a41;
}
.am-tabs-default-bar-tab {
  color: #9f9f9f;
}
.am-tabs-default-bar-tab-active {
  color: #4cbe28;
}
.am-tabs-default-bar-underline {
  display: none;
}
